import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import {
  ApiHttpService,
  NavigationService,
  RequestStatus,
  StateService,
  StopChecksPreferencesService,
} from '@backbase/payord-stop-checks-widget-ang';
import { ChecksService } from '@backbase/payment-orders-ang';
import { StopChecksCreatePreferencesService } from '../../services/stop-checks-create-preferences.service';
import { StopChecksNavigationService } from '../../services/navigation.service';
import { pluck, takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { NgbModalOptions } from '@ng-bootstrap/ng-bootstrap';
import { StopChecksListCommunicationService } from '@backbase/payord-stop-checks-list-widget-ang';

@Component({
  selector: 'bb-stop-checks-create-modal',
  template: `
    <bb-modal-ui
      [isOpen]="true"
      [modalOptions]="modalOptions"
      (cancel)="closeModal()"
    >
      <div class="card card-body">
        <router-outlet></router-outlet>
      </div>
    </bb-modal-ui>
  `,
  providers: [
    {
      provide: StopChecksPreferencesService,
      useClass: StopChecksCreatePreferencesService,
    },
    { provide: NavigationService, useClass: StopChecksNavigationService },
    StateService,
    ApiHttpService,
    ChecksService,
  ],
})
export class StopChecksCreateModalComponent implements OnInit, OnDestroy {
  private readonly destroyed = new Subject();
  readonly modalOptions: NgbModalOptions = {
    centered: true,
    backdrop: 'static',
  };

  private navigateToList() {
    this.router.navigate(['..'], { relativeTo: this.activatedRoute });
  }

  constructor(
    private readonly communicationListService: StopChecksListCommunicationService,
    private readonly router: Router,
    private readonly activatedRoute: ActivatedRoute,
    private readonly stateService: StateService
  ) {}

  ngOnInit() {
    this.stateService.store
      .pipe(pluck('status'))
      .pipe(takeUntil(this.destroyed))
      .subscribe((status) => {
        if (
          status === RequestStatus.REQUEST_IS_SUBMITTED ||
          status === RequestStatus.DISCARD
        ) {
          this.communicationListService.refreshListSub.next();
          this.navigateToList();
        }
      });
  }

  ngOnDestroy() {
    this.destroyed.next();
  }

  closeModal() {
    this.navigateToList();
  }
}
