import { Inject, Injectable, Optional } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { AccessControlConfigProvider } from '@backbase/payment-orders-ang';
import {
  BB_STOP_CHECK_ACCESS_CONTROL_CONFIG_TOKEN,
  defaultStopChecksCreatePreferences,
} from '@backbase/payord-stop-checks-widget-ang';
import { StopChecksJourneyService } from './stop-checks-journey-config.service';
import { SchemeName } from '@backbase/data-ang/stop-checks';

/** @internal */
@Injectable()
export class StopChecksCreatePreferencesService {
  private readonly config = {
    ...defaultStopChecksCreatePreferences,
    ...this.journeyConfig.configuration,
    ...(this.accessControlToken || {}),
  };

  private readonly resourceNameSub$ = new BehaviorSubject<string>(
    this.config.resourceName
  );
  private readonly businessFunctionSub$ = new BehaviorSubject<string>(
    this.config.businessFunction
  );
  private readonly privilegeSub$ = new BehaviorSubject<string>(
    this.config.privilege
  );
  private readonly notificationDismissTimeSub$ = new BehaviorSubject<
    number | undefined
  >(this.config.notificationDismissTime);
  private readonly currenciesSubject$ = new BehaviorSubject<
    string[] | undefined
  >(this.config.currencies);
  private readonly checksRangeNumberSubject$ = new BehaviorSubject<
    number | undefined
  >(this.config.checksRangeNumber);
  private readonly schemeNameSubject$ = new BehaviorSubject<
    SchemeName | undefined
  >(this.config.schemeName);

  readonly resourceName$ = this.resourceNameSub$.asObservable();
  readonly businessFunction$ = this.businessFunctionSub$.asObservable();
  readonly privilege$ = this.privilegeSub$.asObservable();
  readonly notificationDismissTime$ =
    this.notificationDismissTimeSub$.asObservable();
  readonly currencies$ = this.currenciesSubject$.asObservable();
  readonly checksRangeNumber$ = this.checksRangeNumberSubject$.asObservable();
  readonly schemeName$ = this.schemeNameSubject$.asObservable();

  constructor(
    @Optional()
    @Inject(BB_STOP_CHECK_ACCESS_CONTROL_CONFIG_TOKEN)
    private readonly accessControlToken: AccessControlConfigProvider,
    private readonly journeyConfig: StopChecksJourneyService
  ) {}
}
