import { ModuleWithProviders, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BackbaseCoreModule } from '@backbase/foundation-ang/core';
import {
  PayordStopChecksListWidgetAngModule,
  StopChecksListCommunicationService,
} from '@backbase/payord-stop-checks-list-widget-ang';
import { StopChecksJourneyComponent } from './stop-checks-journey.component';
import { provideRoutes, Route, RouterModule } from '@angular/router';
import { StopChecksListComponent } from './components/stop-checks-list/stop-checks-list.component';
import { StopChecksJourneyService } from './services/stop-checks-journey-config.service';
import { StopChecksCreateModalComponent } from './components/stop-checks-create-modal/stop-checks-create-modal.component';
import { PayordStopChecksWidgetAngModule } from '@backbase/payord-stop-checks-widget-ang';

import { StopChecksCreateFormComponent } from './components/stop-checks-create-form/stop-checks-create-form.component';
import { CommunicationListService } from './services/comunication-list.service';
import { StopChecksReviewFormComponent } from './components/stop-checks-review-form/stop-checks-review-form.component';
import { ModalModule } from '@backbase/ui-ang/modal';
import { HeaderModule } from '@backbase/ui-ang/header';

export const StopChecksDefaultRoute: Route = {
  path: '',
  component: StopChecksJourneyComponent,
  children: [
    {
      path: '',
      redirectTo: 'list',
      pathMatch: 'full',
    },
    {
      path: 'list',
      component: StopChecksListComponent,
      children: [
        {
          path: 'form',
          component: StopChecksCreateModalComponent,
          children: [
            {
              path: '',
              pathMatch: 'full',
              component: StopChecksCreateFormComponent,
            },
            { path: 'review', component: StopChecksReviewFormComponent },
          ],
        },
      ],
    },
  ],
};

/**
 * @deprecated Please use StopChecksDefaultRoute instead
 */
export const stopChecksRoutes = StopChecksDefaultRoute;

@NgModule({
  declarations: [
    StopChecksJourneyComponent,
    StopChecksListComponent,
    StopChecksCreateModalComponent,
    StopChecksCreateFormComponent,
    StopChecksReviewFormComponent,
  ],
  imports: [
    CommonModule,
    ModalModule,
    BackbaseCoreModule.withConfig({
      classMap: {
        StopChecksJourneyComponent,
      },
    }),
    RouterModule,
    HeaderModule,
    PayordStopChecksListWidgetAngModule,
    PayordStopChecksWidgetAngModule,
    ModalModule,
  ],
  providers: [
    StopChecksJourneyService,
    {
      provide: StopChecksListCommunicationService,
      useClass: CommunicationListService,
    },
  ],
})
export class StopChecksJourneyModule {
  static forRoot(
    data: { route: Route; [key: string]: any } = {
      route: StopChecksDefaultRoute,
    }
  ): ModuleWithProviders<StopChecksJourneyModule> {
    return {
      ngModule: StopChecksJourneyModule,
      providers: [provideRoutes([data.route])],
    };
  }
}
